import React, { Component } from 'react';
import classnames from 'classnames';
import PictureHero from '../Picture/Hero';
import DetailsInfo from '../Details/Info';
import Paper from '../../UI/Paper';
import { post } from '../type';

export default class HeroSplash extends Component {

  static propTypes = {
    post: post.isRequired,
  };

  render() {
    const { post, className, color, backgroundColor, children } = this.props;

    return (
      <section className={classnames('hero-splash', className)}>
        <PictureHero post={post} />
        <Paper className="hero-splash__details" style={{ backgroundColor, color }} elevation={16}>
          <DetailsInfo post={post} />
          {children}
        </Paper>
      </section>
    );
  }
}



