import React, { Component } from 'react';
import {} from 'prop-types';
import classnames from 'classnames';
import EventListener from 'react-helpful/EventListener';
import { doc } from '../../utils/browser';

export default class Viewer extends Component {
  state = {
    active: false,
  };

  toggleView = (src, backgroundImage) => {
    const active = !this.state.active;
    this.setState({ active, src, backgroundImage });
    document.body.classList.toggle('viewer--no-scroll');
  };

  onOverlayClick = () => {
    this.toggleView('');
  };

  onImageClick = event => {
    const link = event.target.parentNode;
    const background = link.querySelector('.gatsby-resp-image-background-image');
    const backgroundImage = background.style.backgroundImage;
    const src = link.href;
    this.toggleView(src, backgroundImage);
  };

  render() {
    const { active, src, backgroundImage } = this.state;

    return (
      <div className={classnames('viewer', { 'viewer--active': active })} onClick={this.onOverlayClick}>
        <EventListener
          target={doc}
          selector=".gatsby-resp-image-wrapper"
          event="click"
          on={this.onImageClick}
          prevent={true}
        />
        <img className="viewer__media" src={src} style={{ backgroundImage }} />
      </div>
    );
  }
}



