import React, { Component } from 'react';
import Viewer from '../components/Viewer/Viewer';
import Page from '../components/Page';
import Post from '../components/Post/Post';
import Google from '../components/Ad/Google';
import Recommendations from '../components/Post/Recommendations';
import Comments from '../components/Comments/Disqus';

export default class BlogPostTemplate extends Component {

  render() {
    const { post, recommended } = this.props.pathContext;
    if (typeof yaCounter28756646 === 'object') {
      yaCounter28756646.reachGoal(`Category_${post.category}`);
    }

    return (
      <Page
        title={post.title}
        description={post.description}
        path={post.slug}
        image={post.images.heroPreviewLG.src}
        date={post.date}
      >
        <Post post={post} />
        {!!recommended.length && <Recommendations posts={recommended} />}
        <Google client="ca-pub-3437520002188474" slot="3696929149" format="auto" />
        {!!post.nocomments || <Comments post={post} />}
        <Viewer />
      </Page>
    );
  }
}


