export function hexToRGB(color) {
  if (color.length === 4) {
    let extendedColor = '#';
    for (let i = 1; i < color.length; i++) {
      extendedColor += color.charAt(i) + color.charAt(i);
    }
    color = extendedColor;
  }

  const values = {
    r: parseInt(color.substr(1, 2), 16),
    g: parseInt(color.substr(3, 2), 16),
    b: parseInt(color.substr(5, 2), 16)
  };

  return 'rgb(' + values.r + ', ' + values.g + ', ' + values.b + ')';
}

export function decompose(color) {
  if (color.charAt(0) === '#') {
    return decompose(hexToRGB(color));
  }

  const marker = color.indexOf('(');
  const type = color.substring(0, marker);
  const values = color.substring(marker + 1, color.length - 1).split(',');

  return {
    type: type,
    values: values.map(value => parseFloat(value))
  };
}

export function getLuminance(color) {
  color = decompose(color);

  if (color.type.indexOf('rgb') > -1) {
    const rgb = color.values.map(val => {
      val /= 255; // normalized
      return val <= 0.03928 ? val / 12.92 : Math.pow((val + 0.055) / 1.055, 2.4);
    });
    return Number((0.2126 * rgb[0] + 0.7152 * rgb[1] + 0.0722 * rgb[2]).toFixed(3)); // Truncate at 3 digits
  } else if (color.type.indexOf('hsl') > -1) {
    return color.values[2] / 100;
  }
}

export function getContrastRatio(foreground, background) {
  const lumA = getLuminance(foreground);
  const lumB = getLuminance(background);
  const contrastRatio = (Math.max(lumA, lumB) + 0.05) / (Math.min(lumA, lumB) + 0.05);

  return Number(contrastRatio.toFixed(2)); // Truncate at two digits
}

export function getContrastColor(background, light, dark) {
  const contrastRatio1 = getContrastRatio('#fff', background);
  const contrastRatio2 = getContrastRatio('#555', background);
  return contrastRatio1 > contrastRatio2 ? light : dark;
}


