import React, { Component } from 'react';
import {} from 'prop-types';

export default class Chip extends Component {
  render() {
    const { children } = this.props;
    return (
      <div className="chip">
        <span className="chip__label">
          {children}
        </span>
      </div>
    );
  }
}



