import React, { Component } from 'react';
import Helmet from 'react-helmet';
import Link from 'gatsby-link';
import classnames from 'classnames';
import { getContrastColor } from '../../utils/color';
import Chip from '../UI/Chip';
import HeroSplash from './Preview/HeroSplash';
import Share from './Share';
import Google from '../Ad/Google';

import { post } from './type';

export default class Post extends Component {

  static propTypes = {
    post: post.isRequired,
  };

  render() {
    const { post, className } = this.props;

    const backgroundColor = post.color;
    const color = getContrastColor('#3a5c88', '#fff', '#333');

    return (
      <div className={classnames(className, 'post')}>
        <HeroSplash post={post} color={color} backgroundColor={backgroundColor}>
          <h2 style={{ color }}>{post.title}</h2>
          <Share post={post} color={color} />
        </HeroSplash>
        <div className="post__content">
          <Google client="ca-pub-3437520002188474" slot="3696929149" format="auto" />
          <article dangerouslySetInnerHTML={{ __html: post.html }} />
          <div className="post__tags">
            <i className="material-icons">local_offer</i>
            {post.tags.map(tag => <Link to={`/tags/${tag}/`} key={tag}><Chip className="post__tag">{tag}</Chip></Link>)}
          </div>
        </div>
        <Helmet>
          <meta name="tags" content={post.tags} />
          <meta name="category" content={post.category} />
        </Helmet>
      </div>
    );
  }
}



