import React from 'react';
import { Row, Col } from '../UI/Grid';
import { siteUrl } from '../../../config.json';

export default function Share(props) {
  const { post, color } = props;
  return (
    <Row className="share">
      <Col>
        <a className="i-light fa fa-facebook fa-4x" style={{ color }} href={`https://www.facebook.com/sharer/sharer.php?app_id=778520525558698&u=${post.permalink}&t=${post.title}&display=popup&ref=plugin&src=potofcode`} target="fb" />
      </Col>
      <Col>
        <a className="i-light fa fa-vk fa-4x" style={{ color }} href={`http://vk.com/share.php?url=${post.permalink}&title=${post.title}&image=${post.images.heroPreviewMD.src}`} target="vk" />
      </Col>
      <Col>
        <a className="i-light fa fa-twitter fa-4x" style={{ color }} href={`https://twitter.com/intent/tweet?url=${post.permalink}&text=${post.title}&via=potofcode&hashtags=potofcode`} />
      </Col>
      <Col>
        <a className="i-light fa fa-google-plus fa-4x" style={{ color }} href={`https://plus.google.com/share?url=${post.permalink}`} />
      </Col>
    </Row>
  );
}



