import React, { Component } from 'react';
import classnames from 'classnames';

export default class Paper extends Component {
  static defaultProps = {
    elevation: 4
  };
  render() {
    const { elevation, className, style, children } = this.props;

    return (
      <div className={classnames('paper', `paper--elevation-${elevation}`, className)} style={style}>
        {children}
      </div>
    );
  }
}




